.cv_one_side p,
.cv_one_side li {
  font-size: 0.9rem;
}

.cv_one_side h3 {
  font-size: 1.3rem;
}

.cv_one_side {
  background-color: var(--clr-bg-alt);
  padding: 0.5em 1.3em;
  font-family: 'Popp5ins', sans-serif;
  line-height: 1.3;
  /* color: var(--clr-fg); */
  /* background-color: var(--clr-bg); */
}

.complete-name {
  font-size: 1.7rem;
  color: var(--clr-primary);
  text-align: center;
  margin-bottom: 0.4rem;
}

.contact-info {
  text-align: center;
  margin-bottom: 0.4rem;
}

.about-me {
  text-align: justify;
}

.section {
  margin-top: 0.6rem !important;
}

.section_title {
  font-size: 1rem !important;
  border-bottom: 0.15rem solid var(--clr-fg);
  text-transform: uppercase;
  color: var(--clr-primary);
  margin: 0 0 0.5rem 0;
}
.section_item {
  margin: 0 0 0.7rem 0;
}

.section_item_title {
  font-weight: bolder;
  white-space: unset;
  text-wrap: balance;
}

.section_item_subtitle {
  font-weight: 500;
}

.section_item_date {
  font-weight: bold;
  color: var(--clr-primary);
  font-size: 0.8rem !important;
}
/* .section_item_location{} */

.section_item_description_list {
  list-style-type: square;
  list-style-position: inside;
  margin-top: 0.3rem;
  text-align: justify;
}
/* .section_item_description_item{} */

.section_item_description_list_item {
  margin: 0.3rem 0;
}

.certifications__grid {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));
  grid-gap: 0.5em;
}

.text_end {
  text-align: end;
}

.certificate_item {
  border-bottom: 0.1em solid #f7f7f7;
}

.page_break {
  break-after: region;
  page-break-inside: avoid;
} /* page-break-after works, as well */

.page_break_inside {
  page-break-inside: avoid;
}

.page_no_break {
  break-before: avoid;
}

.no_padding {
  padding: 0 !important;
}

.li_small {
  font-size: 0.8rem !important;
}
