.skills__list {
  max-width: 450px;
  width: 95%;
  margin: 0.5rem auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.skills__list-item {
  margin: 0.5em;
}

.skills__title {
  margin-top: 1em;
  font-size: 1.1rem;
  font-weight: bold;
  text-align: center;
}

.skills__grid {
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));
  grid-gap: 1em;
}

@media (max-width: 900px) {
  .skills__grid {
    grid-template-columns: repeat(auto-fit, minmax(18em, 1fr));
  }
}

.btn {
  padding: 0.6em 1.1em !important;
}
