.grade {
  padding: 2em;
  margin: 0 auto;
  text-align: center;
  box-shadow: var(--shadow);
  transition: transform 0.2s linear;
  display: flex;
  flex-direction: column;
}

.grade:hover {
  transform: translateY(-7px);
}

.grade__date {
  margin: 1.7em 0;
  font-weight: 500;
  font-size: 0.8rem;
  color: var(--clr-fg-alt);
}

.grade__title {
  margin-top: 1em;
  font-size: 1.1rem;
  white-space: unset;
  text-wrap: balance;
  /* font-weight: 600;
    font-size: 1.1rem; */
}

.grade__institution {
  white-space: unset;
  text-wrap: balance;
}

.grade__logo {
  align-self: center;
  margin-bottom: 1rem;
}

.grade__logo > img {
  object-fit: contain;
  background: transparent;
}
